.header{
    background-color: #301818;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.escritas{
    display: flex;
    text-align: end;
    font-size: 25px;
}

.escrita_h{
    color: #D7C7AE;
    text-decoration: none;
    padding:10px;
    height: 15px;
}

.escrita_h:hover{
    color: #8F191F;
    transition: all 0.5ms;
}

.header img {
    width: 80px;
    margin: 20px;

}