.produtos{
    margin: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    justify-content: space-between;
    justify-items: center;
}

.trufas, .trufas1, .trufas2, .trufas3{
    background-color: #8F191F;
    width: 255px;
    padding: 25px;
    border-radius: 10px;
}

.infot img {
    border-radius: 10px;
}

.infot p {
    color: #EFE8DA;
    font-size: 20px;
}

.link{
    text-decoration: none;
}